export default "/* Style the radio button */\nion-radio {\n  /* Set width and height to 0 to hide the default radio button */\n  width: 0;\n  height: 0;\n}\n/* Style the custom radio button container */\nion-radio::part(container) {\n  /* Set the container to be a circle */\n  border-radius: 50%;\n  width: 20px;\n  /* Set the width of the circle */\n  height: 20px;\n  /* Set the height of the circle */\n  border: 2px solid #ddd;\n  /* Border color and width */\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  position: relative;\n  /* Add this to position the dot relative to the container */\n}\n/* Style the selected dot */\nion-radio.radio-checked::part(container)::after {\n  content: \"\";\n  /* Create a pseudo-element for the dot */\n  background-color: #656269;\n  /* Color of the selected dot */\n  border-radius: 50%;\n  /* Make the dot a circle */\n  width: 10px;\n  /* Set the width of the dot */\n  height: 10px;\n  /* Set the height of the dot */\n  position: absolute;\n  /* Position the dot relative to the container */\n  top: 50%;\n  /* Center the dot vertically */\n  left: 50%;\n  /* Center the dot horizontally */\n  transform: translate(-50%, -50%);\n  /* Move the dot back by half its size to center it */\n}\n/* Hide the default check mark */\nion-radio::part(mark) {\n  display: none;\n}\n/*# sourceMappingURL=data:application/json;base64, */";